@import 'variables/welcome_variables';
@import 'keyframes/_all';

@keyframes color {
	0% {
		background: $brand-primary;
		color: #000;
	}
	25% {
		background: $brand-danger;
	}

	50% {
		background:$brand-info;
	}

	75% {
		background: $brand-success;
	}
	100% {
		background: #fff;
	}
}

@keyframes reversecolor {
	0{
		background: #fff;
	}
	25% {
		background: $brand-success;
		color: #000;
	}
	50% {
		background: $brand-info;
	}

	75% {
		background:$brand-danger;
	}

	100% {
		background: $brand-primary;
	}
}

#welcome{
	height: 100%;
	width: 100%;
	background: $body-bg;
	
	.loader{
		height:100%;
		width: 100%;
		background: #000;
		position:fixed;
		top: 0px;
		left: 0px;
		right: 0;
		bottom: 0;
		z-index:2;
		display:flex;
		justify-content:center;
		padding-top:300px;
		
		.cp-meter {
			-webkit-animation: color 5s linear infinite;
			   -moz-animation: color 5s linear infinite;
			     -o-animation: color 5s linear infinite;
			        animation: color 5s linear infinite;
		}
	}
	
	#header{
		height: $w-header-height;
		width: $w-header-width;
		background: $w-header-bg;
		padding-left: $w-header-padding-left;
		padding-right: $w-header-padding-right;
		position: $w-header-position;
		opacity: $w-header-opacity;
		
		.brand-name {
			a{
				color: $w-header-brand-color;
			}
		}

		.menu-list{
			padding:$w-header-list-padding;
			left: 0;
			.list-item {
				display:$w-header-list-item-display;
				margin-left: $w-header-list-item-margin;
				a{
					color: $w-header-list-item-anchor-color;
				}
			}
		}
	}

	#head-section{
		min-height: $head-section-height;
		padding-top: $head-section-padding-top;
		width: $head-section-width;
		background: $head-section-image;
		-webkit-background-size: $head-section-image-size;
		     -o-background-size: $head-section-image-size;
		        background-size: $head-section-image-size;
		
		font-family: $font;
		color: #FFF;
		
		.row.headline-row{
			height: auto;
			width: auto;
			margin-bottom: 250px;
			-webkit-animation: fadeDown 5s;
			   -moz-animation: fadeDown 5s;
			     -o-animation: fadeDown 5s;
			        animation: fadeDown 5s;
		}

		.row.information {
			height: auto;
			width: auto;
			-webkit-animation: fadeUp 5s;
			   -moz-animation: fadeUp 5s;
			     -o-animation: fadeUp 5s;
			        animation: fadeUp 5s;
		}
	}	
}